html {
  height: -webkit-fill-available;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  border: none;
  font-family: var(--font-main);
}
body {
  background: var(--component-background);
  background-size: cover;
  overflow-x: hidden;
  height: -webkit-fill-available;
}
.App {
  height: -webkit-fill-available;
}
a {
  color: unset;
  text-decoration: none;
}
@keyframes slide {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
